import React, {Component} from "react";
import {PropTypes} from "prop-types";
import {LCol, LRow} from "./Utils";
import UniversalProductDetails from "./FixProductDetails";
import {GraphicCell, NameCell, PriceCell} from "./modalFragments/FixInputFragments";
import {Section, Trigger} from "../../../../../core/components/ui/Expander";
import FixTvFilteredExpander from "eshop/modules/fix/components/fragments/FixTvFilteredExpander";

export default class FixProductImmutableBox extends Component {
    constructor(props) {
        super(props);
    }

    getSectionHeader() {
        return (
            <Trigger
                className="js-expander__trigger__nested u-inline-block large-offset-by-3 medium-offset-by-3 u-padding-all-l u-no-padding-top">
                <span className="js-expander__trigger--show">{'Zwiń\u00a0szczegóły'}</span>
                <span className="js-expander__trigger--hide">{'Szczegóły'}</span>
            </Trigger>
        );
    }

    getIncludedInThePackageTitle() {
        if (this.props.includedInThePackageTitle && this.props.includedInThePackageTitle !== "") {
            return this.props.includedInThePackageTitle;
        }
        return "W cenie pakietu";
    }

    isWww() {
        return !!this.props.channels && this.props.channels.sales === "WWW";
    }

    render() {
        const includedInThePackageTitle = this.getIncludedInThePackageTitle();

        return (<div>
                <div className="u-position-relative g-white1-bg is-selected">
                    {this.props.vas.selected &&
                    <div className="opl-selection-layer" aria-hidden="true">
                        <div className="opl-selection-layer__border"></div>
                        <div className="opl-selection-layer__checkmark"></div>
                    </div>
                    }

                    <LRow
                        className="u-no-margin u-padding-all-l u-no-padding-bottom u-small-padding-all u-small-padding-top-l"
                        onClick={this.props.onSelectionChanged}>
                        <GraphicCell vas={this.props.vas}/>
                        <NameCell vas={this.props.vas}/>
                        {
                            this.props.showPrice
                                ?
                                <PriceCell small="12" medium="5" big="5" vas={this.props.vas} net={this.props.net}/>
                                :
                                <LCol small="12" medium="5" big="5"
                                      className="u-large-text-left u-medium-text-left u-small-padding-l">
                                    <p className="g-brand2-c h4"
                                       dangerouslySetInnerHTML={{__html: includedInThePackageTitle}}/>
                                </LCol>
                        }
                    </LRow>
                    <Section header={this.getSectionHeader()}
                             contentClassName="js-expander__container__nested"
                             headerClassName="u-position-relative u-small-text-right"
                             className="l-row u-no-margin opl-section-outer--2"
                             expanded={this.isWww()}
                             styleObject={this.isWww() ? {display: "block"} : null}>
                        <UniversalProductDetails vas={this.props.vas}/>
                    </Section>
                </div>

                {!!(this.props.vas && this.props.vas.addonType==="TVBASIC" && this.props.idx===0) &&
                    <FixTvFilteredExpander tvPackages={this.props.tvPackages}/>}

            </div>
        );
    }
}

FixProductImmutableBox.propTypes = {
    vas: PropTypes.shape({
        title: PropTypes.string,
        id: PropTypes.string,
        paymentDescriptions: PropTypes.shape({
            monthlyPayments: PropTypes.array
        })
    }),
    idx: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    onSelectionChanged: PropTypes.func,
    showPrice: PropTypes.bool,
    selected: PropTypes.bool,
    includedInThePackageTitle: PropTypes.string
}
